<template>
    <h1>Lista</h1>
    <label>Tarefa:</label>
    <input type="text" v-model="tarefa" 
     placeholder="Digite a sua tarefa">
    <button @click="salvaTarefa()">Salvar</button>
</template>
<script>
    export default{
        name: "formLista",
        data(){
            return{
                tarefa:"",
                tarefas:[]
            }
        },
        methods:{
            salvaTarefa(){
                if(this.tarefa==""){
                    alert("Favor preencher o campo tarefa");
                    return;
                }
                this.tarefas.push(this.tarefa);
                console.log(this.tarefas);
                this.tarefa="";
                //Evento personalizado
                this.$emit('atualizaVetor', this.tarefas);
            }
        }
    }
</script>