<template>
    <table>
        <tr>
            <td>Tarefa</td>
            <td></td>
        </tr>
        <tr v-for="(item, i) in tarefas" :key="i">
            <td>{{ item }}</td>
            <td><button @click="apagaItem(i)">Apagar</button></td>
        </tr>
    </table>
</template>
<script>
    export default{
        name: "exibeLista",
        data(){
            return{
                
            }
        },
        props: ["tarefas"],
        methods:{
            apagaItem(indice){
                this.$emit('remover', indice);
            }
        }
    }
</script>