<template>
  <formLista @atualizaVetor="novoVetor"></formLista>
  <exibeLista :tarefas="tarefas" @remover="apagaTarefa"></exibeLista>
</template>

<script>
import formLista from './components/formLista.vue'
import exibeLista from './components/exibeLista.vue';

export default {
  name: 'App',
  components: {
    formLista, exibeLista
  },
  data(){
    return{
      tarefas:[]
    }
  },
  methods:{
    novoVetor(vetor){
      this.tarefas = vetor;
    },
    apagaTarefa(indice){
      this.tarefas.shift(indice);
    }
  }
}
</script>

